<template>
    <v-header>
        <div slot="right" :class="['h-right',isEdit ? '' : 'opa-6']"><a class="mg-r10" @click="submit">提交</a></div>
    </v-header>
    <v-content>
        <div class="certification-box">
            <div class="section">
                <div class="matter">
                    <p class="title">认证技能</p>
                    <div class="edit" v-link="{name:'menu-all',query:{from:from,type:type}}">
                        <input class="text" placeholder="请选择技能类别(必填)" v-model="categoryName"/>
                    </div>
                    <span class="img transfer"></span>
                </div>
            </div>

            <div class="section">
                <div class="matter">
                    <p class="title">证书名称</p>
                    <div class="edit">
                        <input type="text" placeholder="请填写证书名称(必填)" v-model="name" class="name keyup" maxlength="20"/>
                    </div>
                </div>
                <div class="matter">
                    <p class="title">证书编号</p>
                    <div class="edit">
                        <input type="text" placeholder="请填写证书编号(必填)" v-model="code" class="code keyup" maxlength="20"/>
                    </div>
                </div>
                <div class="matter">
                    <p class="title">有效期限</p>
                    <div class="edit">
                        <div v-if="deadLine != ''" class="deadLine keyup" readonly="readonly" @click="selectDeadLine">
                            {{deadLine}}
                        </div>
                        <!--<input v-if="deadLine != ''" type="text" placeholder="请填写有效期限" v-model="deadLine" class="deadLine keyup" readonly="readonly" @click="selectDeadLine"/>-->
                        <div v-else class="deadLine keyup" readonly="readonly" @click="selectDeadLine">永久</div>
                    </div>
                </div>
                <div class="matter">
                    <p class="title">颁发机构</p>
                    <div class="edit">
                        <input type="text" placeholder="请填写颁发机构(必填)" v-model="org" class="org keyup" maxlength="25"/>
                    </div>
                </div>
                <div class="pic-area">
                    <p class="title">认证照片</p>
                    <div class="pic-view">
                        <div v-for="item in items" :class="[$index % 3 == 0 ? 'first':'']"
                             style="width:{{picSize}}px;height:{{picSize}}px;background-image:url({{item.imgPath}});">
                            <span class="img del" @click="del($index,item.id)"></span>
                        </div>
                        <div class="add-pic" style="width:{{picSize}}px;height:{{picSize}}px;">
                            <input type="file" accept="image/*" class="upload opa-hide" id="upload">
                            <span class="img add"></span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </v-content>
    <v-date-component></v-date-component>
</template>
<style scoped>
    .pic-area {
        padding: .15rem .1rem .25rem;
        background-color: #fff;
        overflow: hidden;
    }

    .pic-area .title {
        font-size: .16rem;
        margin-bottom: .1rem;
    }

    .pic-view > div {
        position: relative;
        margin-left: .05rem;
        margin-top: .05rem;
        float: left;
        background-size: 100% 100%;
    }

    .pic-view > div:first-child {
        margin-left: 0;
    }

    .pic-view > div.first {
        margin-left: 0;
    }

    .pic-view > div:not(:last-child):after {
        content: '';
        position: absolute;
    }

    .pic-view > div:last-child {
        border: .01rem solid #E5E5E5;
    }

    .add-pic .upload {
        position: absolute;
        width: 100%;
        height: 100%;
    }

    .img.add {
        position: absolute;
        top: 50%;
        left: 50%;
        margin-left: -.125rem;
        margin-top: -.125rem;
        width: .25rem;
        height: .25rem;
        background-size: .25rem;
        background-image: url(../../../assets/img/icon/add_btn.png);
    }

    .img.del {
        position: absolute;
        right: -.03rem;
        top: -.03rem;
        width: .19rem;
        height: .19rem;
        background-size: .19rem;
        background-image: url(../../../assets/img/icon/del02.png);
    }
</style>
<script>
    import {url, lt, st, obj} from '../../../const'
    import {imgHost} from '../../../filter'
    import {toast} from '../../../assets/js/toast'
    import {DJUtils} from '../../../assets/js/common'
    import VDateComponent from '../../../components/DateComponent'
    import VHeader from '../../../components/Header'
    import VContent from '../../../components/Content'
    import $ from 'jquery'
    require('../../../assets/js/lrz.all.bundle');
    export default{
        route: {
            data({from, to}){
                let vm = this;
                let size = $(window).width() - 20 - 5 * 2;
                vm.picSize = size / 3;


                vm.from = vm.$route.path;
                let isNew = JSON.parse(vm.$route.query.isNew);
                if (isNew) {
                    if (!st.has(st.keys.certificationEdit)) {
                        let cert = obj.certification;
                        cert.backEnd = vm.dataToStr();
                        st.set(st.keys.certificationEdit, cert);
                    }
                    vm.buildData(st.getJson(st.keys.certificationEdit));
                    vm.isEdit = vm.checkEdit();
                } else {
                    let id = vm.$route.params.id;
                    let news = st.getJson(st.keys.certificationEdit);
                    //修改的发布和请求的发布一样时 直接拿本地的发布
                    if (news && news.id == id && news.getTime + 1800 >= parseInt(new Date().getTime() / 1000)) {
                        vm.buildData(news);
                    } else {
                        this.$http.get(url.getCertDetail, {id: id}).then(
                            function ({data:{success, data, resultMsg}}) {
                                if (success) {
                                    let cer = vm.buildObj(data);
                                    vm.buildData(cer);
                                    cer.backEnd = vm.dataToStr();
                                    st.set(st.keys.certificationEdit, cer);
                                    vm.isEdit = vm.checkEdit();
                                } else {
                                    toast.alert(resultMsg);
                                }
                            }, function (response) {
                                toast.alert('网络异常');
                            }
                        );
                    }

                }
            }
        },
        data(){
            return {
                id: 0,
                name: '',
                category: 0,
                categoryName: '',
                code: '',
                deadLine: '',
                org: '',
                status: 0,

                type: 4,//编辑
                from: '',
                backEnd: '',
                isEdit: false,

                picSize: 0,
                items: [],
                delIds: [],
                imgChange: 0
            }
        },
        ready(){
            let vm = this;
            $("#upload").click(function () {
                if (vm.items.length >= 5) {
                    toast.alert('每个证书最多只能上传5张照片');
                    return false;
                }
                $(this).val('');
            });
            $("#upload").change(function () {
                if (!this.files || !this.files.length) return;
                var files = this.files[0];
                var obj = this;
                if (!/image\/\w+/.test(files.type)) {
                    toast.alert("图片格式不正确，请选择正确格式的图片文件！");
                    return false;
                } else {
                    var fileReader = new FileReader();
                    fileReader.onload = function (e) {
                        let ua = navigator.userAgent.toLowerCase();
                        if (/android/.test(ua)) {
                            toast.loadEnd();
                            var base64 = this.result;
                            vm.items.push({
                                imgPath: base64,
                                id: null
                            });
                            //同步ss
                            vm.syncSessionStorage();
                            vm.imgChange = 1;
                            vm.isEdit = vm.checkEdit();
                        } else {
                            lrz(files, {width: 667})
                                .then(function (rst) {
                                    toast.loadEnd();
                                    var base64 = rst.base64;
                                    vm.items.push({
                                        imgPath: base64,
                                        id: null
                                    });
                                    //同步ss
                                    vm.syncSessionStorage();
                                    vm.imgChange = 1;
                                    vm.isEdit = vm.checkEdit();
                                })
                                .catch(function (err) {
                                    toast.alert("图片处理失败");
                                });
                        }
                    };
                    fileReader.onerror = function (e) {
                        toast.alert("图片加载失败");
                    };
                    fileReader.readAsDataURL(files);
                    toast.loading();
                }
            });

            $(".keyup").blur(function () {
                let _this = $(this);
                if (_this.is('.name') && !vm.name) {
                    toast.alert('证书名称不能为空');
                }
                if (_this.is('.code') && !vm.code) {
                    toast.alert('证书编号不能为空');
                }

                if (_this.is('.deadLine') && (vm.deadLine && !DJUtils.checkDateFmt(vm.deadLine))) {
                    toast.alert('日期格式不正确');
                }
                if (_this.is('.org') && !vm.org) {
                    toast.alert('颁发机构不能为空');
                }
                vm.isEdit = vm.checkEdit();
            });

            $(".keyup").keyup(function () {
                console.log('keyup...');
                vm.syncSessionStorage();
                vm.isEdit = vm.checkEdit();
            });

        },
        methods: {
            selectDeadLine (){
                this.$broadcast('init-time', 3, '选择有效日期', this.deadLine);
            },
            syncSessionStorage (){
                let vm = this;
                let cert = st.getJson(st.keys.certificationEdit);
                cert.name = vm.name;
                cert.code = vm.code;
                cert.deadLine = vm.deadLine;
                cert.org = vm.org;
                cert.items = vm.items;
                cert.delIds = vm.delIds;
                st.set(st.keys.certificationEdit, cert);
            },
            del(index, id){
                let vm = this;
                console.log('id:' + id);
                this.items.splice(index, 1);
                if (id) {
                    this.delIds.push(parseInt(id));
                    this.syncSessionStorage();
                    this.imgChange = 1;
                }
                vm.isEdit = vm.checkEdit();
            },
            submit(){
                let vm = this;
                if (!vm.isEdit) {
                    return;
                }
                let dataStr = vm.dataToStr();
                let backEnd = st.getJson(st.keys.certificationEdit).backEnd;

                if (dataStr == backEnd) {
                    toast.alert('数据无变化');
                    return;
                }

                this.$http.get(url.editCertification, vm.buildPostParams()).then(
                    function ({data: {data, success, resultMsg}}) {
                        if (success) {
                            toast.alert('提交成功');
                            DJUtils.backHistory();
                        } else {
                            toast.alert(resultMsg);
                        }
                    });
            },

            checkEdit (){
                if (!this.category) {
                    return false;
                } else if (!this.name) {
                    return false;
                } else if (this.deadLine && !DJUtils.checkDateFmt(this.deadLine)) {
                    return false;
                } else if (!this.org) {
                    return false;
                } else if (this.items.length == 0) {
                    return false;
                }
                return true;
            },
            buildPostParams (){
                let vm = this;
                let img = '';
                let items = vm.items;
                for (let i = 0; i < items.length; i++) {
                    if (!items[i].id) {
                        img += (items[i].imgPath + ' ');
                    }
                }
                let delIds = '';
                let delIdsArr = vm.delIds;
                for (let i = 0; i < delIdsArr.length; i++) {
                    if (delIdsArr[i]) {
                        if (delIdsArr.length - 1 == i) {
                            delIds += (delIdsArr[i]);
                        } else {
                            delIds += (delIdsArr[i] + ',');
                        }
                    }
                }

                let params = {
                    id: vm.id,
                    name: vm.name,
                    category: vm.category,
                    code: vm.code,
                    deadLine: vm.deadLine == '永久' ? '' : vm.deadLine,
                    org: vm.org,
                    img: img,
                    delIds: delIds
                };
                return params;
            },

            buildData (item){
                if (item) {
                    this.id = item.id;
                    this.name = item.name;
                    this.category = item.category;
                    this.categoryName = item.categoryName;
                    this.code = item.code;
                    this.deadLine = item.deadLine;
                    this.org = item.org;
                    this.status = item.status;
                    this.items = item.items;
                    this.delIds = item.delIds ? item.delIds : [];
                    this.imgChange = item.imgChange ? item.imgChange : 0;
                }
            },
            buildObj (data){
                let imgItems = [];
                if (data.items) {
                    for (var index in data.items) {
                        let imgItem = data.items[index];
                        imgItem.imgPath = imgHost(imgItem.imgPath);
                        imgItems.push(imgItem);
                    }
                }

                return {
                    id: data.id,
                    name: data.name,
                    category: data.category,
                    categoryName: data.categoryName,
                    code: data.code,
                    deadLine: data.deadLine,
                    org: data.org,
                    status: data.status,
                    items: imgItems,
                    getTime: parseInt(new Date().getTime() / 1000)
                };
            },
            dataToStr(){
                let vm = this;
                let data = {
                    id: this.id,
                    category: vm.category,
                    categoryName: vm.categoryName,
                    name: vm.name,
                    code: vm.code,
                    deadLine: vm.deadLine,
                    org: vm.org,
                    status: vm.status,
                    imgChange: vm.imgChange
                };
                let backEnd = '';
                for (let i in data) {
                    backEnd += (data[i] + '');
                }
                return backEnd;
            }
        },
        events: {
            'backtip': function () {
                let vm = this;
                let dataStr = vm.dataToStr();
                let backEnd = st.getJson(st.keys.certificationEdit).backEnd;
                if (dataStr == backEnd) {
                    DJUtils.backHistory();
                } else {
                    toast.conform('数据已修改,确定退出吗？', function () {
                        DJUtils.backHistory();
                    }, true, null, {do: '退出', cancel: '取消'});
                }
            },
            'deadLine': function (date) {
                this.deadLine = date;
                this.syncSessionStorage();
            }
        },
        components: {VHeader, VContent, VDateComponent}
    }
</script>



